class HomeController {
  constructor() {
    this.name = 'home';

    this.availableClasses = [ 'xp', 'win7', 'win10' ];
    this.classIndex = 0;

    this.className = this.getClassName();

  }

  getClassName() {
  	return this.availableClasses[ this.classIndex ];
  }

  nextClassName(){
  	this.classIndex++;
  	this.className = this.getClassName();
  }

}

export default HomeController;



